import React, { useState } from "react";
import "../../scss/admin/_modals.scss";

const StatusUpdateModal = ({ request, onClose, onSave }) => {
  const [formData, setFormData] = useState({
    status: request.status,
    adminNote: request.adminNote || "",
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    onSave(formData);
  };

  const getStatusName = (status) => {
    const statusMap = {
      pending: "",
      processing: " ",
      completed: "",
      failed: "",
    };

    return statusMap[status] || status;
  };

  const getStatusClass = (status) => {
    return status.toLowerCase();
  };

  return (
    <div className="modal-overlay">
      <div className="admin-modal">
        <div className="modal-content">
          <div className="modal-header">
            <h3>   #{request.orderId}</h3>
            <button className="close-btn" onClick={onClose}>
              &times;
            </button>
          </div>

          <form className="admin-form" onSubmit={handleSubmit}>
            <div className="modal-body">
              <div className="current-status ">
                <span className="label"> :</span>
                <span
                  className={`status-badge ${getStatusClass(request.status)}`}
                >
                  {getStatusName(request.status)}
                </span>
              </div>

              <div className="form-group">
                <label htmlFor="status"> </label>
                <select
                  id="status"
                  name="status"
                  value={formData.status}
                  onChange={handleChange}
                >
                  <option value="pending"></option>
                  <option value="processing"> </option>
                  <option value="completed"></option>
                  <option value="failed"></option>
                </select>
              </div>

              <div className="form-group">
                <label htmlFor="adminNote"></label>
                <textarea
                  id="adminNote"
                  name="adminNote"
                  value={formData.adminNote}
                  onChange={handleChange}
                  placeholder="    ()"
                ></textarea>
              </div>

              {formData.status === "completed" && (
                <div className="status-warning success">
                  <i className="fas fa-info-circle"></i>
                     ""    
                       
                  .
                </div>
              )}

              {formData.status === "failed" && (
                <div className="status-warning danger">
                  <i className="fas fa-exclamation-triangle"></i>
                     ""    
                  .     
                  .
                </div>
              )}
            </div>

            <div className="modal-footer">
              <button
                type="button"
                className="btn btn-outline"
                onClick={onClose}
              >
                
              </button>
              <button type="submit" className="btn btn-primary">
                
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default StatusUpdateModal;
